
import axios from 'axios'
import { defineComponent } from 'vue'
import { userStore, User } from '../stores/user'

export default defineComponent({
	name: 'Dashboard',
	computed: {
		admin() {
			return userStore.get.user?.isAdmin
		},
	},
	data(): {
		message?: string
		users: User[]
		editing: {
			id: number
			name: string
		}
		adding: {
			isAdding: boolean
			name: string
			email: string
		}
		deleting: number
	} {
		return {
			users: [],
			editing: {
				id: 0,
				name: '',
			},
			adding: {
				isAdding: false,
				name: '',
				email: '',
			},
			deleting: 0,
		}
	},
	methods: {
		get() {
			axios.get('/users').then(res => {
				if (res.data.users) {
					this.users = res.data.users
				}
			})
		},
		add(e: Event) {
			e.preventDefault()
			axios.post('/users', this.adding).then(res => {
				if (res.data.message) {
					this.message = res.data.message
				}

				this.adding.isAdding = false
				this.adding.name = ''
				this.adding.email = ''

				this.get()
			})
		},
		setEditing(user: User) {
			this.editing.id = user.id
			this.editing.name = user.name
		},
		edit() {
			axios.patch(`/users/${this.editing.id}`, this.editing).then(() => {
				this.editing.id = 0
				this.editing.name = ''

				this.get()
			})
		},
		remove() {
			axios.delete(`/users/${this.deleting}`).then(() => {
				this.deleting = 0

				this.get()
			})
		},
	},
	mounted() {
		this.get()
	},
})
